			<topHeaderAdmin />
		</div>
		<div class="contain">
			<div class="left scrollbar">
				<menusList />
			</div>
			<div class="right">
				<cityTable v-if="table_id == 0" />
				<!-- <indexCon v-if="currTable_id == 0" />
				<casesTable v-else-if="currTable_id == 1"/>
				<imagesTable v-else-if="currTable_id == 2" />	
				<diagnosisTable v-else-if="currTable_id == 3"	/>	
				<checksTable v-else-if="currTable_id == 4" />
				<trainsTable v-else-if="currTable_id == 5" />
				<algorithmTable v-else-if="currTable_id == 6" />
				<renamesTable v-else-if="currTable_id == 7" />
				<repeatedTable v-else /> -->
