	import cityTable from "../../template/admin/cityTable/index"
	// import casesTable from "../../template/casesTable/index"
	// import diagnosisTable from "../../template/diagnosisTable/index"
	// import imagesTable from "../../template/imagesTable/index"
	// import renamesTable from "../../template/renamesTable/index"
	import menusList from '../../template/menusList/index'
	// import repeatedTable from '../../template/repeatedTable/index'
	// import checksTable from '../../template/checksTable/index'
	// import trainsTable from '../../template/trainsTable/index'
	// import algorithmTable from '../../template/algorithmTable/index'

	export default({
		name: 'admin',
		data(){
			return {

			}
		},
		computed:{
			table_id: function(){
				return this.$store.state.table_id;
			}
		},
		components: {
			menusList,
			topHeaderAdmin,
			cityTable,
		},
		created: function(){
			var that = this;		
		}
	})
</script>

<style scoped="scoped">
	/*.index{position: fixed;width: 100%;height: 100%;}*/
	.contain{height: 100%;display: flex;margin-top: 20px;}
	.left{min-width: 155px;overflow-y:auto; }
	.right{width:100%;border: 1px solid #e6e6e6;margin: 0 20px;padding: 20px;margin-bottom: 20px;min-width: 1000px;}
</style>


