<template>
	<div class="cityTable">
		<div class="btn">
			<el-button type="primary" @click="add">新增</el-button>	
		</div>
		<el-table
			    :data="tableData"
			    style="width: 100%"
			    :max-height="800">
		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>
		    <el-table-column
		      prop="id"
		      label="id"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="name"
		      label="名称"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="imgUrl"
		      label="图片路径"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="isForeign"
		      label="是否为国内城市"
		      >
		      <template slot-scope="scope">
		      	<span :style="scope.row.isForeign?'color: green':'color: red'">{{ scope.row.isForeign?'true':'false' }}</span>
		      </template>
		    </el-table-column>
		    <el-table-column
		      fixed="right"
		      label="操作"
		      >
		      <template slot-scope="scope">
		        <el-button
		          @click.native.prevent="updateRow(scope.$index, tableData)"
		          size="small">
		          更新
		        </el-button>
		        <el-button
		          @click.native.prevent="deleteRow(scope.$index, tableData)"
		          type="danger"
		          size="small"">
		          删除
		        </el-button>
		      </template>
		    </el-table-column>
		</el-table>
		<el-dialog :title="isAdd?'新增城市信息':'更新城市信息'" :visible.sync="dialogFormVisible" :append-to-body="true" :center="true">
		  <el-form :model="form">
		    <el-form-item label="id" :label-width="formLabelWidth" v-if="!isAdd">
		      {{ form.id }}
		    </el-form-item>
		    <el-form-item label="城市名称" :label-width="formLabelWidth">
		    	<el-input v-model="form.name" autocomplete="off"></el-input>		    	
		    </el-form-item>
		    <el-form-item label="图片" :label-width="formLabelWidth">
		    	<el-input v-model="form.imgUrl" autocomplete="off"></el-input>
		    	<el-upload
				  class="upload-demo"
				  action="http://www.zhanjf.xyz:8888/uploads"
				  :before-upload="beforeUpload"
				  :on-success="successUpload"
				  :limit="1"
				  >
				  <el-button size="small" type="primary">点击上传</el-button>
				  <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
				</el-upload>	    	
		    </el-form-item>
		    <el-form-item label="是否为国内城市" :label-width="formLabelWidth">
		    	<el-input v-model="form.isForeign" autocomplete="off"></el-input>		    	
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false">取 消</el-button>
		    <el-button type="primary" @click="submit">确 定</el-button>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
	export default({
		name: 'cityTable',
		data() {
			return {
				tableData: [],
				dialogFormVisible: false,
				formLabelWidth: '150px',
				form: {},
				isAdd: true

			}
		},
		created: function(){

		},
		mounted: function(){
			this.getCity();
		},
		methods: {
			getCity() {
				var that = this;
				that.$axios.get(that.API+'/api/user/city').then(res => {
					// console.log(res.data)
					that.tableData = res.data.result;
				}).catch(err => {

				})
			},
			add() {
				this.isAdd = true;
				this.dialogFormVisible = true;
				this.form = {}
			},
			updateRow(index, tableData) {
				this.form = JSON.parse(JSON.stringify(this.tableData[index]));
				this.isAdd = false;
				this.dialogFormVisible = true;
				// console.log(tableData[index])
			},
			deleteRow(index, tableData){

			},
			submit() {

			},
			beforeUpload(file) {
				console.log('file-before-upload', file)
			},
			successUpload(res, file, filelist) {
				console.log('res', res)

				this.$message({
					type: 'success',
					message: res.message
				})
				// console.log(res)
			}
		}
	})
</script>

<style scoped>
	.btn{display: flex;justify-content: flex-end;background: #fff;margin-bottom: 20px;}
	.file{z-index: 10;opacity: 1;}
</style>

<style>
	.el-table .cell{text-align: center;}
</style>


